:root {
    --light-brown: #caa378;
    --dark-brown: #0c0704;
    background-color: #0c0704;
}

/* Header & Footer styles for this subsite can be found in the */
/* /src/Components/AlanIreby/HeaderFooter/header-footer.css file*/

.alanireby-container {
    background-color: #0c0704;
    margin: 0 auto;
}

.content-wrapper {
    min-height: calc(100vh - 122px);
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 50px 20px 20px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    gap: 30px;
    background: #CAA378;
    background: linear-gradient(0deg,
        rgb(70, 62, 54) 0%, 
        rgb(43, 36, 28) 30%, 
        rgba(12, 7, 4, 1) 100%
    );
}

/* Homepage Styles */
.content-wrapper.homepage {
    background-color: #0c0704;
    background-image: url('/public/img/alanireby/alanireby-background.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}
.homepage-content {
    padding-top: 20vh;
    max-width: 1200px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    gap: 30px;
}
.homepage-intro {
    width: 100%;
    text-align: left;
}
.homepage-intro .intro-description {
    font-size: 5em;
    font-family: "Source Serif 4", serif;
    color: var(--light-brown);
    letter-spacing: 3px;
}
.homepage-intro .intro-description span {
    color: #fff;
}
.homepage-headshot {
    border-bottom: 5px solid var(--light-brown);
}

/* Biography Page Styles */
.content-wrapper.biography {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.tab-panel {
    display: flex;
    flex-direction: column;
    text-align: left;
    gap: 10px;
    width: 100%;
}
.tab-panel h2 {
    padding-bottom: 10px;
}
.tab-panel h3 {
    border-width: 0;
    padding: 10px 0;
}
.years-list {
    display: flex;
    flex-direction: column-reverse;
    gap: 20px;
}
.year-group {
    display: flex;
    flex-direction: column;
}
.performance-item {
    border-top: 1px solid var(--light-brown);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 0;
}
.performance-item:first-of-type {
    border-width: 0;
}
.performance-item div:first-of-type {
    color: var(--light-brown);
    font-family: "Noto Sans", sans-serif;
}

/* Biography page toggles */
/* Tabs Container: This wraps all radio buttons, labels (buttons), and content panels */
.tabs-container {
  margin-top: 0 auto;
  border: 1px solid var(--light-brown);
  border-radius: 5px;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1200px;
}

/* Hide the actual radio button input */
.tab-radio {
  display: none;
}

/* Style for individual tab "buttons" (the labels) */
.tab-button {
  flex: 1; /* Distribute space evenly among buttons */
  text-align: center;
  padding: 12px 15px;
  cursor: pointer;
  color: var(--light-brown);
  border-right: 1px solid var(--light-brown);
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  white-space: nowrap; /* Prevent text wrapping inside buttons */
  font-weight: bold;
  /* Add a subtle bottom border for the active indicator */
  border-bottom: 2px solid transparent;
}

/* Remove border from the last button */
.tab-button:last-of-type {
  border-right: none;
}

/* Hover state for tab buttons */
.tab-button:hover {
  background-color: #e0e0e0;
}

/* Style for the ACTIVE tab button */
.tab-radio:checked + .tab-button {
  background-color: var(--light-brown);
  color: #fff;
  border-color: var(--light-brown);
}

/* Tab Panels: Containers for the content of each tab */
.tab-panel {
  display: none;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  border-top: 1px solid var(--light-brown);
  order: 99;
}

/* Show the active tab panel:
   This selects the .tab-panel that is a sibling of a checked .tab-radio
   AND is also a sibling of the .tab-button that immediately follows the .tab-radio.
*/
#tab-Theater:checked ~ #panel-Theater,
#tab-Film:checked ~ #panel-Film,
#tab-Television:checked ~ #panel-Television,
#tab-Student-Films:checked ~ #panel-Student-Films,
#tab-Promotional-Videos:checked ~ #panel-Promotional-Videos,
#tab-RadioStation:checked ~ #panel-RadioStation,
#tab-Director:checked ~ #panel-Director
/* ... add a line for every other tab category you have ... */
{
  display: block;
}

/* --- Keep your existing collapsible CSS for years unchanged --- */
/* Base Styles for Collapsible Elements (from previous iteration) */
.collapsible-container {
  margin-bottom: 10px;
  border-radius: 4px;
  overflow: hidden;
}

.collapsible-toggle {
  display: none;
}

.collapsible-header {
  display: block;
  padding: 10px 15px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  user-select: none;
  position: relative;
  font-weight: bold;
}

.collapsible-header h2,
.collapsible-header h3 {
  margin: 0;
  display: inline-block;
  vertical-align: middle;
}

.collapsible-header::after {
  content: '▼';
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%) rotate(0deg);
  transition: transform 0.3s ease;
  font-size: 0.8em;
  color: #caa378; /* Maintain this color for year arrows */
}

.collapsible-toggle:checked ~ .collapsible-header::after {
  content: '▲';
}

.collapsible-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.1s ease-in-out, padding 0.1s ease-in-out;
  padding: 0 15px;
}

.collapsible-toggle:checked ~ .collapsible-content {
  max-height: 2000px;
  padding-bottom: 15px;
  padding-top: 15px;
}

/* Specific styling for nested lists within collapsibles */
/* Note: I've replaced ul/li with div/div in your JSX, so adjust these selectors accordingly */
.performance-item, .entry-item, .tv-film-etc, .film-item, .promo-video-item, .student-film-item {
  padding: 20px 0;
}

.performance-item:last-child, .entry-item:last-child,
.tv-film-etc:last-child, .film-item:last-child,
.promo-video-item:last-child, .student-film-item:last-child {
  border-bottom: none;
}

/* Optional: Different background for nested collapsible headers (years) */
.year-collapse > .collapsible-header {
  font-size: 0.9em;
  padding: 8px 12px;
}

.entry-list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.entry-item {
    display: grid;
    grid-template-columns: 1.5fr 1fr 1fr 1fr;
    gap: 10px;
    justify-content: space-between;
}


/* About Me Page */
.alanireby-about-me {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    gap: 40px;
    font-size: 1.2em;
    margin: 0 auto;
}
.alanireby-about-me img {
    border-radius: 50%;
    border: 1px solid var(--light-brown);
    padding: 10px;
}
.alanireby-about-me .about-summary {
    text-align: left;
}
.alanireby-about-me .about-summary p {
    margin-bottom: 20px;
}

/* Resume Page */
/* General container for the resume sections */
.resume-sections {
    width: 100%;
    max-width: 1200px;
}

/* Styling for each individual section (e.g., "Equity Number", "Employment", "Reviews") */
.resume-section {
    margin-bottom: 25px; /* Space between sections */
    padding-bottom: 15px; /* Padding above the bottom border */
    border-bottom: 1px solid #eee; /* Separator line */
}

/* Remove border from the last section */
.resume-section:last-child {
    border-bottom: none;
}

/* Section titles (e.g., "Equity Number", "Employment") */
.section-title {
    color: var(--light-brown);
    font-size: 1.4em; /* Slightly larger heading */
    margin-bottom: 10px;
    font-weight: 600;
}

/* Add these new styles to your alanireby-styles.css file */

/* --- Grid Display Container --- */
.grid-display-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    margin-bottom: 40px;
    padding: 20px;
    border-radius: 6px;
    background-color: var(--light-brown);
}

/* --- Individual Grid Items (cells) --- */
.grid-item {
    background-color: var(--dark-brown);
    padding: 15px;
    border-radius: 5px;
    text-align: center; /* Center content horizontally within the cell */
    display: flex; /* Use flexbox to easily center content vertically */
    flex-direction: column;
    justify-content: center; /* Vertically center content */
    align-items: center; /* Horizontally center text/content */
    min-height: 80px; /* Ensures a consistent height for all grid cells */
    box-shadow: 0 2px 5px rgba(0,0,0,0.05); /* Subtle shadow for depth */
}

.grid-item-title {
    font-size: 0.95em; /* Slightly smaller font for the grid item headings */
    color: var(--light-brown);
    margin-bottom: 5px;
    font-weight: normal; /* Often, these titles are less prominent */
    text-transform: capitalize; /* Ensures formatting like "Dob" */
}

.grid-item-content {
    font-size: 1.1em; /* Larger, more prominent font for the actual data */
    font-weight: bold;
    color: #fff;
    margin: 0; /* Remove default paragraph margins */
}

/* --- Full-Width Sections --- */
/* This class ensures that these sections take up the full width after the grid.
   They will naturally stack one below the other due to block-level display. */
.full-width-section {
    width: 100%;
    /* No specific grid properties here as they are outside the grid container */
}

/* Ensure existing resume-section styles apply correctly to full-width sections */
.resume-section {
    margin-bottom: 25px;
    padding-bottom: 15px;
    border-bottom: 1px solid var(--light-brown);
    /* Retain other general styles you have for resume sections */
}

.resume-section:last-child {
    border-bottom: none;
}


/* Styling for simple string content (e.g., equity number, height, email) */
.section-content {
    font-size: 1em;
    line-height: 1.5;
    color: var(--light-brown);
    margin-left: 10px; /* Indent slightly */
}

/* Styling for lists (employment, information, training, writings) */
.section-list {
    list-style-type: disc; /* Bullet points */
    margin-left: 30px; /* Indent the list */
    padding-left: 0;
    font-size: 0.95em;
    text-align: left;
}

.section-list li {
    margin-bottom: 5px; /* Space between list items */
    line-height: 1.4;
}

/* Styling for the reviews section */
.reviews-list { /* TARGETING 'reviews-list' as per your JSX */
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: flex-start;
}

/* Styling for each individual review item in the static list */
.review-item { /* TARGETING 'review-item' as per your JSX */
    flex: 0 0 calc(25% - 15px); /* Calc: 100% / 4 items = 25%, then subtract gap for spacing */
    box-sizing: border-box;
    margin-bottom: 20px;
    padding: 15px;
    border: 1px solid var(--light-brown);
    border-radius: 5px;
    background-color: var(--dark-brown);
    box-shadow: 0 2px 5px rgba(0,0,0,0.5); /* Subtle shadow */
}

.resume-loading,
.resume-error,
.resume-empty {
    text-align: center;
    padding: 30px;
    font-size: 1.2em;
    color: var(--light-brown);
    border: 1px solid #ddd;
    border-radius: 8px;
    margin: 20px;
}

.resume-error {
    color: #d9534f; /* Red for errors */
    border-color: #d9534f;
}

.review-text {
    font-style: italic;
    color: var(--light-brown);
    margin-bottom: 5px;
    line-height: 1.6;
}

.review-attribution {
    text-align: right;
    font-weight: 600;
    color: #fff;
    font-size: 0.9em;
    margin-top: 5px;
}

/* Message for sections with no entries */
.no-entries-message {
    color: var(--light-brown);
    font-style: italic;
    margin-left: 10px;
}

/* Contact Form Page */
.content-wrapper.contact {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.contact-form {
    display: flex;
    flex-direction: column;
    max-width: 80%;
    min-width: 50%;
    margin-top: 20px;
    text-align: left;
}
.contact-form div {
    margin-bottom: 20px;
}
.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid var(--light-brown);
    border-radius: 6px;
    box-sizing: border-box;
    font-size: 1em;
    color: var(--light-blue);
    font-weight: 500;
}
.content-wrapper.contact hr {
    width: 75%;
    margin: 30px;
    color: var(--light-blue);
}

/* Buttons */
.btn-bg,
.btn-border {
  margin-top: 20px;
  padding: 16px 32px;
  font-size: 18px;
  font-weight: 500;
  background-color: transparent;
  border-width: 0;
  border-radius: 5px;
}
.btn-bg {
  border: 1px solid var(--light-brown);
  background-color: var(--light-brown);
  color: #fff;
}
.btn-border {
  border: 1px solid var(--light-brown);
  color: #fff;
  margin-right: 10px;
}
.btn-bg:hover {
  border: 1px solid var(--light-brown);
  background-color: transparent;
  color: #fff;
}
.btn-border:hover {
  border: 1px solid var(--light-brown);
  background-color: var(--light-brown);
  color: #fff;
}

@media screen and (max-width: 800px) {
    .content-wrapper {
        padding-top: 30px;
    }
    /* Mobile Home Page Overrides */
    .content-wrapper.homepage {
        flex-direction: column;
    }
    .homepage-headshot img {
        max-width: 100%;
        height: 100vw;
        aspect-ratio: 1;
    }
    .homepage-intro .intro-description {
        font-size: 4em;
        letter-spacing: 1px;
    }

    /* Mobile Biography Page Overrides */
    .performance-list {
        flex-direction: column;
        justify-content: space-between;
        padding: 8px 0;
    }

    /* Mobile Biography Page Overrides */
    .tabs-container {
        flex-direction: column;
    }
    .entry-item {
        grid-template-columns: 1fr;
        border-bottom: 1px solid var(--light-brown);
    }

    /* Mobile Resume Page Overrides */
    .grid-display-container {
        display: grid;
        grid-template-columns: 1fr;
    }
    .review-item {
        flex: 0 0 100%;
        margin-bottom: 15px;
    }

    /* Contact Page Mobile Overrides */
    .content-wrapper.contact {
        padding-bottom: 50px;
    }
    .content-wrapper.contact hr {
        width: 100%;
    }
    .contact-form {
        width: 100%;
        max-width: 100%;
    }
}