@import url('/src/root-styles.css');

.about-container {
    background-color: var(--darkish-blue); 
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    min-height: calc(100vh - 122px);
}
.about-info {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    max-width: 1200px;
    margin-top: 30px;
    font-size: 20px;
    font-weight: 500;
}
.about-info img {
    border-radius: 15px;
}
.about-text {
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: left;
}

@media screen and (max-width: 800px) {
    .about-info {
        flex-direction: column;
    }   
}