@import url('./nav-styles.css');

:root {
    --light-brown: #caa378;
}

/* Wrapper for desktop content (name + desktop nav + hamburger) */
.alan-ireby-header-content-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 100%;
}


.alan-ireby-header-container,
.alan-ireby-footer-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 20px;
}

/* Header Styles */
.alan-ireby-header-container {
  border-bottom: 1px solid var(--light-brown);
  position: relative; /* Needed for absolute positioning of mobile menu */
  z-index: 100; /* Ensure header is above content */
  background-color: #050402;
}
.alan-ireby-header-name a {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 2em;
}
.alan-ireby-header-name a span:nth-child(2) {
  font-family: var(--roboto-flex-font);
  font-weight: 400;
  color: var(--light-brown);
}

.alan-ireby-header-nav ul {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

/* Styles for the active link */
.alan-ireby-header-nav ul li a {
    border-bottom: 2px solid transparent;
    font-weight: 500;
}
.alan-ireby-header-nav ul li a.active {
    color: white;
    font-weight: 600;
    border-bottom: 2px solid var(--light-brown);
}

/* Footer Styles */
.alan-ireby-footer-container {
  font-size: .8em;
  border-top: 1px solid var(--light-brown);
  padding: 20px;
  background-color: #0c0704;
}
.alan-ireby-footer-container .alan-ireby-footer-link {
  color: var(--light-brown);
}
.alan-ireby-footer-container .alan-ireby-footer-link:hover {
  text-decoration: underline;
}
.alan-ireby-footer-socials {
  display: flex;
  gap: 15px;
}