@import url('/src/root-styles.css');

.contact-container {
    background-color: var(--darkish-blue); 
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    min-height: calc(100vh - 122px);
}

.contact-form {
    display: flex;
    flex-direction: column;
    max-width: 80%;
    min-width: 50%;
    margin-top: 20px;
    text-align: left;
}
.contact-form div {
    margin-bottom: 20px;
}
.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 6px;
    box-sizing: border-box;
    font-size: 1em;
    color: var(--dark-blue);
    font-weight: 500;
}