/* image-modal.css */

/* --- Modal Overlay --- */
.image-modal-overlay {
    position: fixed; /* Stays in place regardless of scrolling */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8); /* Semi-transparent black background */
    z-index: 1000; /* Ensures the modal is on top of other content */

    display: flex; /* Use flexbox to center the modal content */
    justify-content: center; /* Horizontally center */
    align-items: center; /* Vertically center */

    /* Optional: Fade in animation */
    opacity: 0;
    animation: fadeIn 0.3s forwards;
}

/* --- Modal Content Container --- */
.image-modal-content {
    position: relative; /* Needed for positioning close button, arrows, and counter */
    max-width: 90vw; /* Maximum width of the content relative to viewport */
    max-height: 90vh; /* Maximum height of the content relative to viewport */
    background: #1a1a1a; /* Dark background for the image display area */
    padding: 60px 20px 40px;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5); /* Stronger shadow for depth */

    display: flex; /* Flexbox for internal layout (image, caption) */
    flex-direction: column; /* Stack image and caption vertically */
    justify-content: center; /* Center image/caption vertically */
    align-items: center; /* Center image/caption horizontally */

    /* Optional: Zoom in animation */
    transform: scale(0.9);
    animation: zoomIn 0.3s forwards;
}

/* --- Image Display --- */
.modal-image {
    max-width: 100%; /* Ensure image doesn't overflow its container */
    max-height: 80vh; /* Max height to leave space for controls/caption */
    object-fit: contain; /* CRUCIAL: Ensures the entire image is visible within its bounds */
    display: block; /* Removes extra space below image */
    margin: 0 auto; /* Center the image horizontally */
    border-radius: 4px; /* Slight roundness for the image itself */
}

/* --- Image Caption --- */
.modal-caption {
    color: #e0e0e0; /* Light gray for caption text */
    font-size: 1.1em;
    margin-top: 15px;
    text-align: center;
    max-width: 90%; /* Prevent very long captions from stretching too wide */
}

/* --- Close Button --- */
.modal-close-btn {
    position: absolute;
    top: 0;
    right: 15px;
    background: none;
    border: none;
    font-size: 2.5em;
    color: #ffffff;
    cursor: pointer;
    z-index: 1020;
    line-height: 1;
    padding: 5px;
    transition: color 0.2s ease;
}

.modal-close-btn:hover {
    color: #cccccc;
}

/* --- Navigation Buttons (Previous/Next) --- */
.modal-nav-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); /* Vertically center */
    background: rgba(0, 0, 0, .7); /* Semi-transparent white */
    color: #fff;
    border: none;
    cursor: pointer;
    font-size: 2em;
    z-index: 1010;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s ease;
    user-select: none;
    padding-bottom: 6px;
    padding-left: 4px;
}

.modal-nav-btn:hover {
    background-color: rgba(0, 0, 0, 0.3);
}

.modal-nav-btn.prev {
    left: 20px;
}

.modal-nav-btn.next {
    right: 20px;
}

/* --- Image Counter --- */
.image-counter {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%); /* Horizontally center */
    color: white;
    background: rgba(0, 0, 0, 0.5); /* Dark semi-transparent background */
    padding: 8px 15px;
    border-radius: 5px;
    font-size: 0.9em;
    z-index: 1010; /* Above image, below close button */
    user-select: none;
}

/* --- Animations (Optional) --- */
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes zoomIn {
    from { transform: scale(0.9); }
    to { transform: scale(1); }
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
    .image-modal-content {
        max-width: 95vw;
        max-height: 95vh;
        padding: 10px;
    }

    .modal-image {
        max-height: 75vh; /* Adjust for smaller screens */
    }

    .modal-close-btn {
        font-size: 2em;
        top: 5px;
        right: 10px;
    }

    .modal-nav-btn {
        width: 40px;
        height: 40px;
        font-size: 1.5em;
        padding-bottom: 6px;
    }

    .modal-nav-btn.prev {
        left: 10px;
        padding-left: 0;
    }

    .modal-nav-btn.next {
        right: 10px;
        padding-left: 2px;
    }

    .image-counter {
        font-size: 0.8em;
        padding: 6px 12px;
    }

    .modal-caption {
        font-size: 1em;
        margin-top: 10px;
    }
}