/* image-gallery.css */
.content-wrapper {
    min-height: calc(100vh - 122px);
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 50px 20px 20px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    gap: 30px;
    background: #CAA378;
    background: linear-gradient(0deg,
        rgb(70, 62, 54) 0%, 
        rgb(43, 36, 28) 30%, 
        rgba(12, 7, 4, 1) 100%
    );
}

/* --- Image Gallery Grid Container --- */
.image-gallery-grid {
    max-width: 1400px;
    margin: 0 auto;
    display: grid;
    /* Responsive grid columns: auto-fill tries to fit as many columns as possible,
       minmax ensures columns are at least 150px wide, but grow equally to fill space. */
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 15px; /* Space between grid items */
    padding: 20px; /* Padding around the entire gallery grid */
    border-radius: 8px;
    background: #CAA378;
    background: linear-gradient(0deg,
        rgb(70, 62, 54) 0%, 
        rgb(43, 36, 28) 30%, 
        rgba(12, 7, 4, 1) 100%
    );
}

/* --- Individual Thumbnail Container --- */
.gallery-thumbnail-container {
    position: relative; /* Needed for absolute positioning of child elements if any, and for padding-bottom trick */
    width: 100%; /* Take full width of its grid cell */
    padding-bottom: 100%; /* Creates a perfect square container for the image */
    overflow: hidden; /* Ensures image doesn't overflow if it's too big */
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out; /* Smooth hover effect */
}

.gallery-thumbnail-container:hover {
    transform: translateY(-5px); /* Lift effect on hover */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2); /* Enhanced shadow on hover */
}

/* --- Thumbnail Image Itself --- */
.gallery-thumbnail {
    position: absolute; /* Position within the padding-bottom container */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /* CRUCIAL: Crops the image to fill the container without distortion */
    display: block; /* Removes extra space below the image */
    border-radius: 4px; /* Matches container for seamless look */
    transition: transform 0.3s ease; /* For potential zoom on hover */
}

.gallery-thumbnail-container:hover .gallery-thumbnail {
    transform: scale(1.05); /* Slight zoom effect on image hover */
}

/* --- Media Queries for Responsiveness --- */

/* Adjust grid for smaller screens (e.g., tablets) */
@media (max-width: 768px) {
    .image-gallery-grid {
        grid-template-columns: repeat(auto-fill, minmax(120px, 1fr)); /* Smaller thumbnails */
        gap: 10px;
        padding: 15px;
        max-width: 100%;
    }
}

/* Adjust grid for very small screens (e.g., mobile phones) */
@media (max-width: 480px) {
    .image-gallery-grid {
        grid-template-columns: repeat(auto-fill, minmax(100px, 1fr)); /* Even smaller thumbnails */
        gap: 8px;
        padding: 10px;
    }
}